body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 20px;
}

.linkedin-scraper {
  max-width: 600px;
  margin: 0 auto;
  background: white;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.title {
  text-align: center;
  color: #333;
}

.scraper-form {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 20px;
}

.url-input {
  padding: 10px;
  width: 96%;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
}

.submit-button {
  padding: 10px 15px;
  background-color: #0073b1; /* LinkedIn Blue */
  color: white;
  border: none;
  border-radius: 4px;
  width: 100%;
  cursor: pointer;
}

.submit-button:hover {
  background-color: #005582; /* Darker LinkedIn Blue */
}

.error-message {
  color: red;
  text-align: center;
}

.scraped-data {
  margin-top: 20px;
}

.data-title {
  color: #333;
}

.data-content {
  margin: 10px 0;
}

.data-item {
  margin: 5px 0;
}

.experience-list, .education-list, .skills-list {
  list-style-type: none;
  padding: 0;
  text-wrap: wrap;
}

.experience-item, .education-item, .skill-item {
  margin: 10px 0;
  padding: 10px;
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.experience-description {
  margin-top: 5px;
  font-style: italic;
}

.no-data-message {
  color: #666;
}

.suggestions-button {
  margin-top: 20px;
  padding: 10px 15px;
  background-color: #28a745; /* Green */
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.suggestions-button:hover {
  background-color: #218838; /* Darker Green */
}

.suggestions {
  margin-top: 20px;
}
.suggestions-box {
  width: 90%; /* Matches the width of the form */
  max-width: 600px; /* Adjust as needed */
  margin-left: 20px !important;
  padding: 10px; /* Padding for inner content */
  background-color: #f9f9f9; /* Light background color */
  margin-top: 10px; /* Spacing between suggestions and other content */
  overflow: hidden; /* Hide any overflow */
}


.suggestions-title {
  color: #333;
}

.suggestions-content {
  background: #f9f9f9;
  padding: 10px;
  
  border-radius: 4px;
  margin: 0; /* Remove default margin from paragraph */
  word-wrap: break-word; /* Prevent long words from overflowing */
}
.loader {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}


@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
.back-to-top-button{
  border-radius: 5px;
}